@import url(https://fonts.googleapis.com/css2?family=Zen+Maru+Gothic:wght@500&display=swap);

#home-heading {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 1em;
    padding: 5vh 0;
    color: #94C85C;
}
#home-heading > span {
    display: flex;
    flex-direction: column;
    align-items: center;
}
#home-heading .subtitle {
    font-size: .7em;
}

.home-tab {
    padding: none;
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    padding: 0;
    flex-wrap: wrap;
}
.home-tab li {
    list-style: none;
    border-radius: 50%;
    background-color: #D0E6A5;
    text-align: center;
    border: 5px solid #D0E6A5;
}
.home-tab span {
    padding: 5px;
    border-radius: 50%;
    background-color: #F9FCF4;
    display: inline-block;
}
.home-tab a {
    color: #3E6E41;
    text-decoration: none;
    line-height: 100px;
    width: 100px;
    display: inline-block;
    background-color: #D0E6A5;
    border-radius: 50%;
}

@media only screen and (min-width: 720px) {
    body {
        font-size: 1.1em;
    }

    #home-heading {
        font-size: 2em;
    }
}
:root {
    --bdr-rds: 4px;
}

header nav ul {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 7px;
    gap: 7px;
    padding: 0;
    margin: 0;
}
header nav li {
    list-style: none;
    background-color: #D6E4D7;
    border-radius: 4px 4px 0 0;
    border-radius: var(--bdr-rds) var(--bdr-rds) 0 0;
}
header nav li.active {
    background-color: #D0E6A5;
} 
header nav a {
    color: #3E6E41;
    text-decoration: none;
    padding: 10px 7px;
    display: inline-block;
}
header .h1-logo {
    background-color: #D0E6A5;
    margin: 0;
    padding: 6vh 0;
    border-radius: 0 4px 0 0;
    border-radius: 0 var(--bdr-rds) 0 0;
    font-size: 1.5em;
    letter-spacing: .1em;
}
header .h1-logo a {
    text-decoration: none;
    color: #1B3F25;
    font-weight: 500;
    
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

@media only screen and (min-width: 720px) {
    header nav ul {
        grid-gap: 10px;
        gap: 10px;
    }
    header nav a {    
        padding: 20px 15px;
    }
    header .h1-logo {
        font-size: 2em;
    }
}
#form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.5em;
    gap: 1.5em;
}

#form > * {
    box-sizing: border-box;
    flex: 1 1;
    outline: none;

    background-color: #F9FCF4;
    padding: 1.5em;
    color: #1B3F25;
    font-size: 1em;

    border: 1px solid #f1f1f1;
    border-radius: 3px;
}

#form > button {
    background-color: #42BC87;
    color: #fff;
    font-size: 2em;
    padding: .4em;
    border: none;
}

@media only screen and (min-width: 720px) {
    #form {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }

    #form > * {
        padding: .4em;
    }
    #form button {
        max-width: 130px;
    }
    #form select {
        max-width: 200px;
    }
}
.noresult {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
}

.noresult img {
    max-height: 45vh;
}
section.routes {
    background: var(--linearbg);
    padding: var(--margin) 0;
}

.btns-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 1em;
    gap: 1em;
}

.btn-direction {
    box-sizing: border-box;
    border: 2px solid #3E6E41;
    background: transparent;
    color: #3E6E41;
    padding: 7px 35px;
    font-family: 'Zen Maru Gothic', sans-serif;
    font-size: 1em;
    border-radius: 5px;
    cursor: pointer;
}
.btn-dir-active {
    background: #3E6E41;
    color: #D6E4D7;
}

.routes #form {
    padding: 0 var(--margin);
}

.routes .routes-list.result-list {
    padding: 0 var(--margin);
}

.result-list.routes-list li > span:nth-child(1) {
    flex: none;
    border: 1px solid #3E6E41;
    padding: 2px;
}
section.stop {
    background: var(--linearbg);
    padding: var(--margin);
}

.result-list.stop-list li::before {
    content: "\f1b9";
}
section.nearby {
    background: var(--linearbg);
    padding: 0 0 8vh;
    display: flex;
    flex-direction: column;
    grid-gap: 2em;
    gap: 2em;
    color: #1B3F25;
}

.nearby .btn-getlocation {
    -webkit-appearance: none;
            appearance: none;
    border: none;
    outline: none;
    color: #fff;
    background-color: #42BC87;
    padding: 10px 100px;
    border-radius: 4px;
    font-size: 2.5em;
    text-decoration: underline;
    cursor: pointer;
    margin: 0 var(--margin);
}

.nearby > section {
    padding: 0 var(--margin);
}

.result-list.nearby-list li:before {
    content: "\f041";
}
.marker {
    background-image: url(/frog-bus/static/media/maker.8cb80476.svg);
    background-size: cover;
    width: 45px;
    height: 45px;
    cursor: pointer;
}

.nowPosition {
    background-image: url(/frog-bus/static/media/nowPos.df23ec34.svg);
    background-size: cover;
    width: 40px;
    height: 40px;
    cursor: pointer;
    z-index: 500;
}

.busMarker {
    background-image: url(/frog-bus/static/media/bus.e741fff3.svg);
    background-size: cover;
    width: 45px;
    height: 35px;
    cursor: pointer;
}

.mapboxgl-popup {
    max-width: 200px;
    border-radius: 55px;
}

.mapboxgl-popup-content {}
:root {
    --margin: 4.5vh;
    --linearbg: linear-gradient(to bottom, #D0E6A5, #E1E3A3);
}

body {
    font-family: 'Zen Maru Gothic', sans-serif;
    min-height: 100vh;
    background-color: #F9FCF4;
    color: #1B3F25;
}

main {
    padding: 0 5vw;
}

.result-list {
    padding: 0;
    margin: 0;
}
.result-list li::before {
    font-family: 'FontAwesome';
    display: inline-block;
    font-weight: 900;
    margin-right: .7em;
    content: '';
}
.result-list li {
    list-style: none;
    color: #1B3F25;
    margin: 1em 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    grid-gap: .3em;
    gap: .3em;
}
.result-list li > span:nth-child(1) {
    flex: 1 1;
}
